import { toRad } from './helpers.js';

const defaults = {
  symOrder:    8,
  divisions:  12,
  radialGap:  10,
  orientation: 0,
  hue:         0
};

export default class Grid {
  constructor(
    symOrder    = defaults.symOrder,
    divisions   = defaults.divisions,
    radialInc   = defaults.radialGap,
    orientation = defaults.orientation,
    hue         = defaults.hue) {
    this.symOrder    = symOrder;    // order of symmetry
    this.divisions   = divisions;   // no. of divisions in a symmetric sector
    this.radialInc   = radialInc;   // difference in radii of concentric circles
    this.orientation = orientation; // orientation of the grid itself
    this.hue         = hue;         // base color of grid
  }

  draw(canvas) {
    const context = canvas.getContext('2d');

    context.save();

    // move origin to center of canvas
    context.translate(canvas.width / 2, canvas.height / 2);
    
    // draw concentric circles
    for (let r = 0; r < canvas.width / 2; r += this.radialInc) {
      context.save();

      context.strokeStyle = `hwb(${this.hue} 75% 0%)`;

      context.beginPath();
      context.arc(0, 0, r, 0, toRad(360));
      context.closePath();
      context.stroke();
      
      context.restore();
    }
    
    // minor axes
    for (let i = 0; i < this.symOrder * this.divisions; i++) {
      context.save();
      context.rotate(toRad(this.orientation) + toRad(i * (360 / (this.symOrder * this.divisions))));

      context.strokeStyle = `hwb(${this.hue} 50% 0%)`;
      
      context.beginPath();
      context.moveTo(0, 0);
      context.lineTo(canvas.width / 2, 0);
      context.closePath();
      context.stroke();

      context.restore();
    }

    // major axes
    for (let i = 0; i < this.symOrder; i++) {
      context.save();
      context.rotate(toRad(this.orientation) + toRad(i * (360 / this.symOrder)));

      context.strokeStyle = `hwb(${this.hue} 0% 0%)`;
      
      context.beginPath();
      context.moveTo(0, 0);
      context.lineTo(canvas.width / 2, 0);
      context.closePath();
      context.stroke();

      context.restore();
    }
    
    context.restore();
  }
}